<template>
	<view  :class="{show:showMessage==true,hide:showMessage==false}" class="message" :style="{backgroundColor:mmessageType}">
		<text>{{messageText}}</text>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				showMessage: true
			};
		},
		props: {
			messageText: {
				default: '网络异常 !',
				type: String
			},
			mmessageType: {
				default: "#ffb4b4",
				type: String
			}
		},
		mounted() {
			setTimeout(() => {
				this.showMessage = false
			}, 3000)
		}
	}
</script>

<style scoped lang="less">
	.message {
		position: fixed;
		left: calc(50% - 300rpx);
		top: 54px;
		width: 600rpx;
		min-height: 80rpx;
		z-index: 66666;
		border-radius: 5rpx;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.5s;
		opacity: 0;
		text {
			color: #eb4d4b;
		}
	}
	.show{
		animation: anime 0.5s linear;
		opacity: 1 !important;
	}
	.hide{
		animation: anime 0.5s linear reverse;
		opacity: 0 !important;
	}

	@keyframes anime {
		0% {
			top: -100px;
			opacity: 0;
		}
		100% {
			/* #ifdef H5 */
			top: 54px;
			/* #endif */
			/* #ifdef MP-WEIXIN */
			top: 10rpx;
			/* #endif */
			opacity: 1;
		}
	}
</style>
